<template>
  <div :class="['market-item', {'empty':!item.title}]">
    <div v-if="item.title">
      <div class="header"><span>{{ item.title }}</span></div>
      <div class="preview"><img :src="loadIMG()"></div>
      <div class="price"><span>{{ item.price }}</span></div>
      <div class="buy">
        <button @click="buy">Купить</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['item'],
  name: "MarketItem",
  methods: {
    loadIMG() {
      return require('../assets/img/market-item/type-' + this.item.type + '.png')
    },
    buy() {
      //window.mp.events.callRemote('shop', this.item.index);
      //alert("this.item.index");
      window.mp.trigger('menu', 'shop', this.item.index);
      //console.log("Покупка: " + this.item.type + " " + this.item.title);
    }
  }
}
</script>

<style lang="scss">

.market-item {
  display: flex;
  width: 220px;
  height: 283px;

  background: #FFFFFF;

  border: 1px solid #C6C6C6;

  &.empty {
    position: relative;

    &:before {
      content: '';
      display: block;
      width: 193px;
      height: 164px;

      background-image: url(../assets/img/market-item/empty-item.png);
      background-size: cover;

      position: absolute;
      top: 33px;
      left: 6px;
    }
    &:after {
      content: 'нет товара';
      display: flex;
      width: 100%;
      height: 64px;

      justify-content: center;

      color: rgba(0, 106, 85, 0.38);

      font-family: "Proxima Nova Bold", sans-serif;
      font-size: 24px;

      text-shadow: -1px 0 rgba(198, 198, 198, 1), 0 1px rgba(198, 198, 198, 1), 1px 0 rgba(198, 198, 198, 1), 0 -1px rgba(198, 198, 198, 1);

      position: absolute;
      bottom: 0;
      left: 0;
    }
  }

  & > div {
    display: flex;
    width: 220px;
    height: 283px;

    flex-direction: column;

    padding-top: 21px;

    & > .header,
    & > .price {
      display: flex;
      width: 100%;
      height: auto;

      justify-content: center;

      & > span {
        color: rgba(0, 106, 85, 1);

        font-family: "Proxima Nova SemiBold", sans-serif;
        font-size: 18px;
        font-weight: 600;
      }
    }

    & > .preview,
    & > .buy {
      display: flex;
      width: 100%;
      height: 106px;

      justify-content: center;

      margin-top: 9px;
    }

    & > .price {
      margin-top: 22px;
    }

    & > .buy {
      height: 45px;

      margin-top: 18px;

      & > button {
        width: 140px;
        height: 45px;

        background: #404040;
        color: #fff;

        font-family: "Proxima Nova Normal", sans-serif;
        font-size: 18px;

        border: unset;

        &:hover {
          background: #DE1C57;
        }
        &:focus{
          outline: none;
        }
      }
    }
  }
